<script>
  import {mapState} from 'vuex';
  export default {
    name: "iFramePage",
    components: {
    },
    methods: {
      activeSesh() {
        this.timeElapsed = 0;
      },
    },
    watch: {
      // if idle time of a minute passes, go to main home directory page
      timeElapsed: {
        handler(e) {
          if (e >= 60) {
            this.$store.commit("setActiveDirectory", this.homeDirectory);
            this.$router.push("/#index.html");
          }
        }
      },
    },
    computed: {
      ...mapState(['homeDirectory'])
    },
    data() {
      return {
        html: null,
        timeElapsed:0,
      };
    },

    mounted() {
      var htmlField = this.$videos.filter(i => i.fields.ID === window.location.href.split("iframe#")[1])[0].fields.HTML;
      try {
        if (process.env.VUE_APP_BASE_TYPE === "CSV") {
          this.html = this.$html.filter(i => i.title === htmlField)[0].html;
        } else {
          this.html = htmlField;
        }
      } catch(err) {
        console.log(err);
      }
      // Start timer of idle and listener
      setInterval(() => {
          this.timeElapsed += 1;
        }, 1000);
    }
  };
</script>

